:root {
  --selected-label-color: #000000;
  --selected-tab-color: #b9b9b9;
  --hover-color: #b9b9b9;
  --tab-boder-radius: 20px;
  --tab-width: 60px;
  --tab-height: 60px;
}

*,
*:after,
*:before {
  box-sizing: border-box;
}

