  width: 60px;
  width: var(--tab-width);
  font-family: "Inter", sans-serif;
  font-size: 1.25rem;
  font-weight: 500;
  color: gray;
  border-radius: 20px;
  border-radius: var(--tab-boder-radius);
  /* margin: 1%; */
