  transition: 0.25s ease-out;
}

.dimming-mode-tabs_icon__1wEq2 {
  width: 24px;
  height: 24px;
  opacity: 40%;
}

input[type="radio"]:checked + label {
  color: #000000;
  color: var(--selected-label-color);
  font-weight: bold;
  font-size: 1.5rem;
}

input[type="radio"]:checked + label > .dimming-mode-tabs_icon__1wEq2 {
  opacity: 100%;
  transform: scale(1.2);
  transition: 0.25s ease-out;
}

input[id="radio-bright"]:checked ~ .dimming-mode-tabs_glider__wAzWT {
  transform: translateX(0);
}

input[id="radio-day"]:checked ~ .dimming-mode-tabs_glider__wAzWT {
  transform: translateX(100%);
}

input[id="radio-dusk"]:checked ~ .dimming-mode-tabs_glider__wAzWT {
  transform: translateX(200%);
}

input[id="radio-night"]:checked ~ .dimming-mode-tabs_glider__wAzWT {
  transform: translateX(300%);
}

.dimming-mode-tabs_glider__wAzWT {
  position: absolute;
  display: flex;
  height: 60px;
  height: var(--tab-height);
  width: 60px;
  width: var(--tab-width);
  background-color: #b9b9b9;
  background-color: var(--selected-tab-color);
  z-index: 1;
  border-radius: 20px;
  border-radius: var(--tab-boder-radius);
  transition: 0.25s ease-in-out;
}

body {
  margin: 0;
  background: #dddddd;
  font-family: roboto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
